import Vue from 'vue'
import Router from 'vue-router'
import home from './components/home.vue'
import info from './components/info.vue'


Vue.use(Router)

export default new Router({
    mode:'history',
    base:process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/info/:id',
      name: 'info',
      component: info,
      props:true
      
    }
  ]
})