<template>
<div class="info">
    <b-container >
  <b-row>
    <b-col  sm  lg="4">
        <div class="infopiccolumn">
            <div class="infoborder">
                <div class="infopic">
                    <b-img class="infoimage" v-bind:src="results.data.imageUrl" fluid center></b-img>
                </div>
            </div>
        </div>
        </b-col>
    <b-col  sm  lg="8">
        <div class="infocolumn infotext"> 
            <div class="infocolumnborder">
            <h1 class="infotitle"> {{results.data.title}}</h1>     
            <div class="details">
                <ul style="list-style-type:none;">
                 <li>   <h2>{{results.data.defaultArtistName}}</h2>     </li>
                                  
                                 <li>       <h4>{{results.data.description}}</h4></li>
                </ul>
            </div>
                                        <b-list-group class="track"  fluid v-for="(bar,index) in results.data.tracks"  
                                            v-bind:key="index">
                                                <b-col>
                                                    <b-list-group-item id="track-items">
                                                        {{bar.title}}
                                                    </b-list-group-item>
        </b-col>
</b-list-group>
                                    <b-row>
                                        <b-col sm>
                                    <button class="back" @click="redirectToHome" align="right">Back</button>
                                        </b-col>
                                    </b-row>
            </div>
        </div>
    </b-col>
  </b-row>
</b-container>
</div>
</template>
<script>
import axios from 'axios'

export default {
    name:'info',
    props: ['id'],
        data(){
return{
  results:null,
  
}
        },
            mounted () {
    axios
      .get('https://app.lenz.ir:64014/api/v2/content/detail/' + `${this.getmydata()}`)
      .then(response => (this.results = response))
      
  } ,
methods: {
  redirectToHome () {
     this.$router.push(
        {
          name: 'home',
        }
     )
 },
   getmydata(){
  return this.id
},

    
  }
}
</script>
